import React, { Component } from 'react';
import { connect } from 'react-redux';
import { CloudUrl } from '../../../../constants';
import './reaction.scss'
class ReactRipple extends Component {
       render() {
              const { reaction } = this.props
              const numbers = Array.from({ length: 20 }, (_, i) => i + 1);
              return reaction?.status ? <div className={`tb_reaction${reaction?.status ? ' tb_active' : ' tb__hide'}`}>
                     <div className='tb_likes_vew'>
                            <img className='tb_likes_icon' src={`${CloudUrl}/media/icons/heart-message.svg`} height={70} width={70} alt='icon' />
                     </div>
                     <div className={`tb_hearts${reaction?.status ? ' tb__flying' : ''}`}>
                            {numbers.map((number, index) => (<div key={index} className="tb_heart"></div>))}
                     </div>
              </div> : null
       }
}
const mapStateToProps = state => {
       return {
              reaction: state.reaction
       }
}

export default connect(mapStateToProps)(ReactRipple);